import React from "react";
import Cardimg from "../Images/kattie.png";
import starimg from "../Images/star.png";

export default function Card(props) {
  console.log(props);
  return (
    <div className="card">
      <img src={props.img} alt="kattie-img" className="card--img" />
      <div className="card-status">
        <img src={starimg} alt="star-img" className="star-img" />
        <span>{props.rating}</span>
        <span>
          {" "}
          {props.reviewCount}. {props.country}
        </span>
      </div>
      <p className="card--pera">{props.title}</p>
      <p className="card--unit">
        <span className="card-price">From ${props.price} </span>/ person
      </p>
    </div>
  );
}
